const createGridDataModel = require('../../data/model/model');

export const createGridStore = () => {

    createGridDataModel.createGridDataModel();

    // Store data
    var myData = [
      { name : "Cupcake", number:  "305", collectibleAmount: "3.7"},
      { name : "Donut", number:  "452", collectibleAmount: "25.0"},
      { name : "Eclair", number:  "262", collectibleAmount: "16.0"},
      { name : "Frozen yoghurt", number:  "159", collectibleAmount: "6.0"},
      { name : "Gingerbread", number:  "356", collectibleAmount: "16.0"},
      { name : "Honeycomb", number:  "408", collectibleAmount: "3.2"},
      { name : "Ice cream sandwich", number:  "237", collectibleAmount: "9.0"},
      { name : "Jelly Bean", number:  "375", collectibleAmount: "0.0"},
      { name : "KitKat", number:  "518", collectibleAmount: "26.0"},
      { name : "Lollipop", number:  "392", collectibleAmount: "0.2"},
      { name : "Marshmallow", number:  "318", collectibleAmount: "0"},
      { name : "Nougat", number:  "360", collectibleAmount: "19.0"},
      { name : "Oreo", number:  "437", collectibleAmount: "18.0"},
     ];
     
     // Creation of first grid store
     return Ext.create("Ext.data.Store", {
       model: "DataModel",
       data: myData,
       pageSize:5,
       autoLoad: { params: { start: 0, limit: 5 } },
       proxy: {
         enablePaging: true,
         type: "memory",
         reader: {
           type: "json",
         },
       },
     });
}